export default class RestoService {
    _dbJson = 'http://localhost:3000'

    async getResourse(url) {
        const res = await fetch(`${this._dbJson}${url}`)
        console.log(res);
        if (!res.ok) {
            throw new Error(`Could not fetch ${url} from ${this._dbJson}`)
        }

        return await res.json()
    }


    async getMenuItems() {
        return await this.getResourse('/menu/')
    }
}