import React, { Component } from 'react';
import { connect } from 'react-redux'
import WithRestoService from '../hoc'
import { menuLoaded, menuRequested, menuError, addedToCart } from '../../actions'
import Spinner from '../spinner'
import View from './view';
import Error from '../error'

import './menu-list.scss';

class MenuList extends Component {
    componentDidMount() {
        this.props.menuRequested()
        const { RestoService } = this.props
        RestoService.getMenuItems()
            .then(res => this.props.menuLoaded(res))
            .catch(() => this.props.menuError());
    }

    render() {
        const { error, loading, menuItems, addedToCart } = this.props

        if (loading) {
            return <Spinner />
        }

        if (error) {
            return <Error/>
        }

        return <View menuItems={menuItems} onAddToCart={addedToCart}/>
    }
};

const mapStateToProps = (state) => {
    return {
        menuItems: state.menu,
        loading: state.loading
    }
}
//–ö–æ–º–ø–æ–Ω–µ–Ω—Ç menu list —É–º–µ–µ—Ç —á–∏—Ç–∞—Ç—å –∏ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å –¥–µ–π—Å—Ç–≤–∏—è, –∑–∞–ø–∏—Å–∞–Ω–Ω—ã–µ –≤ —Ñ—É–Ω–∫—Ü–∏—é –Ω–∏–∂–µ
const mapDispatchToProps = {
    menuLoaded,
    menuRequested,
    menuError,
    addedToCart
}

export default WithRestoService()(connect(mapStateToProps, mapDispatchToProps)(MenuList));