import React from 'react';
import './cart-table.scss';
import { connect } from 'react-redux';
import { deleteFromCart } from '../../actions';
import img from './empty-cart.ceb2faf0.png'
import { Link } from 'react-router-dom';

const CartTable = ({ items, deleteFromCart }) => {
    function makeBtn() {
        return items.length !== 0 ? <button variant="secondary" className='cart__btn'>–ó–∞–∫–∞–∑–∞—Ç—å</button> : null
    } 

    function ifEmpty() {
        if (items.length !== 0) {
            return <div className="cart__title">–í–∞—à –∑–∞–∫–∞–∑:</div>
        } else {
            return (
                <>
                    <h2 className="cart__title">–ö–æ—Ä–∑–∏–Ω–∞ –ø—É—Å—Ç–∞—è <span role='img' aria-label="smile">üòï</span></h2>
                    <h3 className='cart__descr'>–î–ª—è —Ç–æ–≥–æ, —á—Ç–æ–±—ã –∑–∞–∫–∞–∑–∞—Ç—å –≤–∫—É—Å–Ω—è—à–∫—É, –ø–µ—Ä–µ–π–¥–∏ –Ω–∞ –≥–ª–∞–≤–Ω—É—é —Å—Ç—Ä–∞–Ω–∏—Ü—É.</h3>
                    <img src={img} alt='basket' className='cart__img' />
                    <Link to='/' className='cart__btn-back'>–í–µ—Ä–Ω—É—Ç—å—Å—è –≤ –º–µ–Ω—é</Link>
                </>
            )
        }

    }


    return (
        <>
            {ifEmpty()}
            <div className="cart__list">
                {items.map(item => {
                    const { title, price, url, id, counterIdentical } = item
                    return (
                        <div className="cart__item" key={id}>
                            <img src={url} className="cart__item-img" alt={title}></img>
                            <div className="cart__item-title">{title}</div>
                            <div className='cart__item-counter'>Count: {counterIdentical}</div>
                            <div className="cart__item-price">Total: {counterIdentical * price}$</div>
                            <div onClick={() => deleteFromCart(id)} className="cart__close">&times;</div>
                        </div>
                    )
                })}
                {makeBtn()}
            </div>
        </>
    );
};

const mapDispatchToProps = {
    deleteFromCart
}

const mapStateToProps = ({ items }) => {
    return {
        items
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(CartTable);