import React from 'react'
import MenuListItem from '../menu-list-item'

const View = ({ menuItems, onAddToCart }) => {
    return (
        <ul className="menu__list">
            {
                menuItems.map(menuItem => {
                    return <MenuListItem key={menuItem.id} onAddToCart={() => onAddToCart(menuItem.id)} menuItem={menuItem}/>
                })
            }
        </ul>
    )


}

export default View