import React from 'react';
import { MainPage, CartPage } from '../pages';
import AppHeader from '../app-header';
import { Route, Routes } from 'react-router-dom';

const App = () => {
    return (
        <div style={{ background: 'linear-gradient(0deg, rgba(153,195,34,0.9078131496739321) 0%, rgba(210,184,143,1) 23%, rgba(121,116,76,0.680922393371411) 62%, rgba(253,187,45,0.6837235138195903) 100%)' }} className="app">
            <AppHeader/>
            <Routes>
                <Route path='/' element={<MainPage />} />
                <Route path='/cart' element={<CartPage />} />
            </Routes>
        </div>
    )
}

export default (App);


