import React from 'react';
import './menu-list-item.scss';
import salad from './salad.png'
import pizza from './pizza.png'
import meat from './meat.png'


const MenuListItem = ({ menuItem, onAddToCart }) => {
    const {title, price, url, category} = menuItem
    function setLogo(category) {
        switch(category) {
            case 'salads': return (<img className='logo' src={salad} alt='food'></img> )
            case 'pizza': return (<img className='logo' src={pizza} alt='food'></img>)
            case 'meat': return (<img className='logo' src={meat} alt='food'></img>)
            default: return
        }
    }

    return (
        <li className="menu__item">
            <div className="menu__title">{setLogo(category)}{title}</div>
            <img className="menu__img" src={url} alt={title}></img>
            <div className="menu__category">Category: <span>{category}</span></div>
            <div className="menu__price">Price: <span>{`${price}$`}</span></div>
            <button onClick={() => onAddToCart()} className="menu__btn">Add to cart</button>
        </li>
    )
}

export default MenuListItem;